.map-settings {
	cursor: pointer;
	user-select: none;
	background-color: $color-main-2;

	display: flex;
	align-items: center;
	justify-content: center;

	&--pos-map{
		position: absolute;
		z-index: 1;
		right: 0;
		width: 40px;
		height: 40px;
		border-radius: 15px 15px 0 0;
		top: -40px;


		@include media-breakpoint-up(md) {
			top: -6px;
			border-radius: 0 0 15px 15px;
			left: 50%;
			margin-left: -20px;
			right: auto;
		}

	}

	&:focus{
		outline: none;
	}

	&__icon{
		fill: $color-blue;
		transition: all 0.2s;
		.tippy-active &{
			transform: rotate(45deg);
		}

		svg {
			display: block;
			width: 1.5em;
			height: 1.5em;
		}

		&:focus{
			outline: none;
		}
	}

	&__content{
		text-align: left;
	}

	&__list{
		list-style: none;
	}
	&__item{
		& + & {
			margin-top: 10px;
		}
	}
}

.map {
	position: relative;
	border: 10px solid $color-main-2;
	border-radius: 15px;
	margin: 0 -22px;
	@include media-breakpoint-up(md) {
		margin: 0;
	}

	&__layout{
		&:before{
			padding-bottom: cp(858, 932);
		}
	}

	&__img{
		display: block;
		max-width: 100%;
	}

	&__markers{
		list-style: none;
	}
	&__marker{
		position: absolute;
		font-size: 22px;
		cursor: pointer;

		@include media-breakpoint-up(md) {
			font-size: 40px;
		}

		&:hover {
			z-index: 1;
		}

		&:focus{
			outline: none;
		}
	}
	&__player-icon,
	&__marker-icon{
		position: absolute;
		bottom: 100%;
		left: 50%;
		margin-left: -0.5em;
		transition: all 0.2s ease;

		svg{
			display: block;
		}

		&:focus{
			outline: none;
		}
	}
	&__player-icon{}
	&__marker-icon{
		fill: $color-red;
		&:hover{
			fill: darken($color-red, 5%);
		}

		.tippy-active &{
			transform: scale(0);
		}

		.map--hide-markers &{
			visibility: hidden;
			opacity: 0;
		}
		.map--invisible-markers &{
			opacity: 0.2;
			&:hover{
				// opacity: 1;
			}
		}
	}

	&__cursor-position{
		position: absolute;
		z-index: $zindex-popover;
		margin-top: 10px;
		margin-left: 10px;
		padding: 0 5px;
		border-radius: 15px;
		background-color: rgba(#fff, 0.8);
		color: $color-text;
		white-space: nowrap;
		font-size: 12px;
		// text-shadow: 0 0 1px #000;
	}
}

.user-coords {
	padding: 20px;
	border-radius: 15px;
	background-color: $color-main-2;

	&__input{
		text-align: center;
		width: 100%;
		@include media-breakpoint-up(sm) {
			width: 120px;
		}
	}
	&__btn{
		min-width: 0;
		padding: 0 10px;
	}
}

.marker-tooltip {
	text-align: center;

	&__title{
		font-size: 18px;
		font-weight: $fw-medium;
	}
}