.header{
	background: $color-main-2;
	padding: 16px 0;
	border-radius:15px;
	margin: 5px 5px 0;

	&__logo{
		fill: #fff;
		font-size: 60px;
		&:hover{
			fill: $color-blue;
		}
		svg {
			display: block;
			transition: all 0.2s;
		}
	}

	&__title{
		font-size: 22px;
		font-weight: $fw-semibold;
	}
}

.top-nav {
	&__list{
		@include media-breakpoint-up(md) {
			display: flex;
			align-items: center;
			margin: 0 10px;
		}
	}
	&__item{
		list-style: none;
		margin: 0 10px;
	}
	&__link{}
	&__sub-menu{}
	&__sub-item{}
	&__sub-link{}
}